body {
  transition: background-color .5s;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #4caf50;
  border: none;
  border-radius: 4px;
  padding: 1rem 2rem;
  font-size: 1.2rem;
  transition: background-color .3s;
}

button:hover:not(:disabled) {
  background-color: #3e8e41;
}

button:active:not(:disabled) {
  background-color: #1e5631;
}

